table {
    font-family: arial, sans-serif;
    border-collapse: collapse;
    width: 50%;
  }
  
  td, th {
    border: 1px solid #dddddd;
    text-align: left;
    padding: 8px;
  }
  
  tr:nth-child(even) {
    background-color: #010a27;
  }
  .item_name{
    font-size: 12px;
  }
  .pics{
      height: 100px;
  }
  .inp-box{
      width: 100px;
      color: rgb(15, 15, 15);
  }